import React from "react";
import { StaticQuery, graphql } from "gatsby";
import styled from "styled-components";
import posed from "react-pose";
import Divider from "../core/Divider";

const Section = posed.section({
  enter: {
    beforeChildren: true,
    staggerChildren: 50
  }
});

const charPoses = {
  exit: { opacity: 0 },
  enter: { opacity: 1 }
};

const ContentInner = styled.div``;
const StyledName = styled.div`
  font-weight: 700;
  margin: 0;
`;
const StyledSubtitle = styled.div`
  font-size: 0.5em;
`;
function Bio() {
  return (
    <StaticQuery
      query={bioQuery}
      render={data => {
        const { author, role } = data.site.siteMetadata;

        return (
          <section>
            <ContentInner>
              <Section initialPose="exit" pose="enter">
                <StyledSubtitle>Hello, I am</StyledSubtitle>
                <StyledName>{author}</StyledName>
                <StyledSubtitle>{role}</StyledSubtitle>
              </Section>
            </ContentInner>
            <Divider className="stripe teal" />
          </section>
        );
      }}
    />
  );
}

const bioQuery = graphql`
  query BioQuery {
    avatar: file(absolutePath: { regex: "/profile-pic.jpg/" }) {
      childImageSharp {
        fixed(width: 50, height: 50) {
          ...GatsbyImageSharpFixed
        }
      }
    }
    site {
      siteMetadata {
        author
        role
        social {
          linkedin
          github
        }
      }
    }
  }
`;

export default Bio;
